.message[data-v-78f5f835] {
  position: relative;
  background-color: #fff;
  margin: 20px 30px 20px 5px;
  padding: 10px;
  border-radius: 5px 5px 5px 0;
  box-shadow: 5px 0 16px 0 rgba(0, 0, 0, 0.5);
}
.message[data-v-78f5f835]:after {
    content: '';
    position: absolute;
    background-color: transparent;
    border: 10px solid transparent;
    border-bottom: 6px solid #fff;
    width: 0;
    height: 0;
    left: -10px;
    right: auto;
    bottom: 0;
}
.message[data-v-78f5f835]:first-child {
    margin-top: 0;
}
.message.from-me[data-v-78f5f835] {
    margin-right: 5px;
    margin-left: 30px;
    border-radius: 5px 5px 0 5px;
    box-shadow: -5px 0 16px 0 rgba(0, 0, 0, 0.5);
}
.message.from-me[data-v-78f5f835]:after {
      border: 10px solid transparent;
      border-bottom: 6px solid #fff;
      right: -10px;
      left: auto;
}
.message.error[data-v-78f5f835] {
    background-color: #aa2909;
    color: #d9d9d9;
    padding: 8px;
}
.message.error[data-v-78f5f835]:after {
    border-bottom-color: #aa2909;
}
.message:hover .delete[data-v-78f5f835] {
  opacity: 1;
}
.delete[data-v-78f5f835] {
  position: absolute;
  right: 0;
  top: 0;
  margin: 0 2px;
  opacity: 0;
  color: #df4620;
  -webkit-transition: all .2s;
  transition: all .2s;
  cursor: pointer;
}
.delete[data-v-78f5f835]:hover {
    color: #aa2909;
}
.message.error .delete[data-v-78f5f835] {
  color: #d9d9d9;
}
.message.error .delete[data-v-78f5f835]:hover {
    color: black;
}
.info[data-v-78f5f835] {
  text-align: right;
  color: #999999;
  font-size: 1.4rem;
}
.error-icon[data-v-78f5f835] {
  color: #d9d9d9;
  vertical-align: middle;
}



